.conatiner {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.title {
  font-size: 18px;
  font-weight: bold;
  margin-bottom: 0;
}
.conatiner ul {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}
.conatiner li {
  font-weight: 400;
}

.loading_div {
  width: 100%;
  height: 10px;
  margin: 5px 0;
  border-radius: 5px;
  opacity: 0.5;
}
.w50 {
  width: 50%;
}
.w80 {
  width: 80%;
}
